import { Phone_Svg } from "./PhoneIntro";
import styled, { css } from "styled-components";

import VideoBtn from "../../assets/images/Elements/VideoBtn.svg";

const IntroPhone = styled.div`
  width: 100%;
  max-width: 626px;
  max-height: 725px;

  position: relative;

  @media (max-width: 1200px) {
    width: calc(100% - 10%);
    max-width: 600px;
    margin: auto;
  }
`;

const ButtonVideo = styled.a`
  position: absolute;
  left: -50px;
  bottom: 10px;
  width: 223px;
  height: 223px;
  border-radius: 200px;

  background: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;

  user-select: none;

  & img {
    width: 193px;
    height: 193px;
    aspect-ratio: 1/1;
  }
`;

const PhoneSvg = styled.div`
  display: flex;
  width: 100%;
  transform: translateX(10%);

  @keyframes block_move {
    0% {
      transform: translateY(25px);
      opacity: 0;
    }
    100% {
      transform: translateY(0px);
      opacity: 1;
    }
  }
`;

const PhoneDesktop = () => {
  return (
    <IntroPhone>
      <ButtonVideo>
        <img alt="Посмотреть видео о сервисе" src={VideoBtn} />
      </ButtonVideo>
      <PhoneSvg>{Phone_Svg}</PhoneSvg>
    </IntroPhone>
  );
};

export default PhoneDesktop;
